<script>
    import VueApexCharts from 'vue-apexcharts'

    export default {
        components: {
            VueApexCharts
        },
        data(){
            return {

            }
        },
        methods: {
            upTime(time){
                return this.$moment(time).toNow(true)
            },
            reboot(){
                this.$vs.dialog({
                    type: 'confirm',
                    color: 'danger',
                    title: `Подтвердите действие`,
                    text: 'Перезагрузить контроллер?',
                    acceptText: "Перезагрузить",
                    cancelText: "Отменить",
                    accept: this.rebootController
                })
            },
            rebootController(){
                this.$http.get('/settings/reboot')
            }
        },
    }
</script>
